/*------------------------------------*\
		#VARIABLES
\*------------------------------------*/


/**
 * FONT SIZING
 */

$font-size-root: 				0.625rem;
$line-height-base: 			1.618;
$line-height-headings: 	1.2;

$font-size-body: 1.618rem;

$heading-sizes: (
	1: 5.545rem,
	2: 4.236rem,
	3: 3.427rem,
	4: 2.618rem,
	5: 2.148rem,
	6: 1.618rem
);

$font-size-code: 1.25rem;

/**
 * FONT WEIGHTS
 */

$font-weight-default: 	400;
$font-weight-headings: 	700;

/**
 * FONT FAMILY
 */

$font-copy: 		'Montserrat', 'Arial', sans-serif;
$font-headings: 'Montserrat', 'Arial', sans-serif;

/**
 * BASE SPACING VALUE
 */

$base-space: ($line-height-base) * ($font-size-body);

/**
 * BREAKPOINTS
 */

$breakpoints: (
	xsmall: 	400px,
	small: 		640px,
	medium: 	768px,
	large: 		960px,
	xlarge: 	1200px
);

/**
 * GRID
 */

$grid-gutter: ($base-space);

/**
 * COLORS
 */

$colors: 	(
	black: 				#212121,
	dark-grey: 			#424242,
	mid-grey: 			#BDBDBD,
	light-grey: 		#F1F1F1,
	white: 				#FAFAFA,
	red: 				#dd4056,
	darkRed:			#BF3749,
	pink: 				#863150,
	darkBlue: 			#081668,
	lightBlue: 			#0D2296,
	indigo: 			#3F51B5,
	blue: 				#2196F3,
	cyan: 				#00BCD4,
	green: 				#4CAF50,
	yellow: 			#FFEB3B,
	orange: 			#FF9800,
	brown: 				#795548,
	violet:				#1230A8,
	darkPurple:			#301c41,
	lightPurple:		#5230A8,
	homeBlue:			#061047
);

$theme-color-1: 	map-get($colors, red);
$theme-color-2: 	map-get($colors, pink);
$theme-color-3: 	map-get($colors, darkBlue);
$theme-color-4: 	map-get($colors, lightBlue);
$theme-color-5:     map-get($colors, darkRed);
$theme-color-6:     map-get($colors, violet);
$theme-color-7:     map-get($colors, darkPurple);
$theme-color-8:     map-get($colors, lightPurple);
$theme-color-9:     map-get($colors, homeBlue);

$headings-color: 	$theme-color-1;
$text-color: 			map-get($colors, black);

/**
 * TRANSITIONS
 */

$ease-in: 		cubic-bezier(0.755, 0.05, 0.855, 0.06);
$ease-out: 		cubic-bezier(0.23, 1, 0.32, 1);
$ease-in-out: cubic-bezier(0.86, 0, 0.07, 1);

$transition-ease: 	'out';
$transition-speed: 	0.45;